        Description: "El helenismo se caracterizó por el hecho de que se borraron las fronteras entre los distintos países y culturas. Anteriormente los griegos, romanos, egipcios, babilonios, sirios y persas habían adorado a sus dioses dentro de lo que se suele llamar «religión de un Estado nacional». Ahora las distintas culturas se mezclan en un crisol de ideas religiosas, filosóficas y científicas.\n\nPodríamos decir que la plaza se cambió por la arena mundial. También en la vieja plaza habían resonado voces que llevaban diferentes mercancías al mercado así como diferentes ideas y pensamientos. Lo nuevo fue que las pla\u00ADzas de las ciudades ahora se llenaban de mercancías e ideas del mundo entero, y que se oían muchas lenguas dis\u00ADtintas.\n\nYa hemos mencionado que las ideas griegas se sem\u00ADbraron mucho más allá de las antiguas zonas de cultura griega. Pero, a la vez, por toda la región mediterránea tam\u00ADbién se rendía culto a dioses orientales. Surgieron varias nuevas religiones que recogían dioses e ideas de algunas de las antiguas naciones. Esto se llama sincretismo, o mez\u00ADcla de religiones.Anteriormente la gente se había sentido muy unida a su pueblo y a su ciudad-estado. Pero conforme esas separaciones y líneas divisorias se fueron borrando, mucha gente tenía dudas y se sentía insegura ante las visiones y conceptos de la vida. Esa parte de la Antigüedad estaba, en términos generales, caracterizada por la duda religiosa, la desintegración religiosa y el pesimismo. «El mundo está viejo», se decía.Una característica común de las nuevas religiones del helenismo era que solían tener una teoría, a menudo secreta, sobre cómo las personas podían salvarse de la muerte. Aprendiendo esas teorías secretas y realizando, además, una serie de ritos, las personas podían tener esperanza de obtener un alma inmortal y una vida eterna. El adquirir unos determinados conocimientos sobre la verdadera naturaleza del universo podía ser tan importante como los ritos religiosos para salvar el alma.Éstas fueron las religiones, Sofía, pero también la filosofía se movía cada vez más hacia la salvación y el consuelo. Los conocimientos filosóficos no sólo tenían un valor en si mismos, también debían librar a los seres humanos de su angustia vital, de su miedo a la muerte y de su pesimismo. De esta manera se borraron los límites entre religión y filosofía.En general podemos decir que la filosofía helenística era poco original. No surgió ningún Platón ni ningún Aristóteles. Pero por otra parte los tres grandes filósofos de Atenas fueron una importante fuente de inspiración para varias corrientes filosóficas, de cuyos rasgos principales te haré un pequeño resumen.También en la ciencia del helenismo se notaba la mezcla de ingredientes de diferentes culturas. La ciudad de Alejandría en Egipto jugó en este contexto un papel clave como lugar de encuentro entre Oriente y Occidente. Atenas continuó siendo la capital de la filosofía con las escuelas filosóficas heredadas de Platón y Aristóteles, y Alejandría se convirtió en el centro de la ciencia. Con su gran biblioteca, esta ciudad fue la capital de las matemáticas, la astronomía, biología y medicina.Se podría muy bien comparar el helenismo con la cultura del mundo actual. También el siglo XX se ha caracterizado por una sociedad mundial cada vez más abierta. También en nuestro tiempo esto ha llevado a grandes cam\u00ADbios en cuanto a religión y conceptos sobre la vida. De la misma manera que se podían encontrar ideas de divinida\u00ADdes griegas, egipcias y orientales en Roma a principios de nuestra era, podemos ahora, hacia finales del siglo XX, en\u00ADcontrar ideas religiosas de todas partes del mundo en todas las ciudades europeas de cierto tamaño.\n\nTambién en nuestro tiempo vemos cómo una mezco\u00ADlanza de religiones viejas y nuevas, de filosofías y ciencias, puede formar la base para nuevas ofertas en el «mercado de las grandes ideas sobre la vida». Gran parte de esos «nue\u00ADvos conocimientos» son en realidad productos viejos del pensamiento, con algunas raíces en el helenismo.\n\nComo ya he mencionado, la filosofía helenística con\u00ADtinuó trabajando en ideas y planteamientos tratados por Sócrates, Platón y Aristóteles. Los tres intentaban buscar la manera más digna y mejor de vivir y de morir para los seres humanos. Es decir, se trataba de la ética. En la nueva socie\u00ADdad mundial ése fue el proyecto filosófico más importante:\n\n¿en qué consiste la verdadera felicidad y cómo la podemos conseguir? Ahora vamos a ver cuatro corrientes filosóficas que se ocuparon de esta cuestión."
